<template>
  <footer class="music-playing"
          @click="$store.dispatch('show')">
    <div class="thum">
      <img src="static/images/thum.jpg">
    </div>
    <div class="music-info">
      <p>青花瓷</p>
      <p>周杰伦</p>
    </div>
    <div class="play-icon">
      <i :class="!$store.state.playing?'icon-play': 'icon-stop'"
        @click.stop="play()"></i>
      <i class="icon-menu">&#xe62f;</i>
    </div>
  </footer>
</template>

<script>
  export default {
    name: "music-playing",
    computed:{
      show() {
        return this.$store.state.data;
      }
    },
    methods:{
      play(){
        this.$store.dispatch('playing');
        this.$store.getters.playState;
      }
    }
  }
</script>

<style scoped>
  .music-playing {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2.5rem;
    border-top: 1px solid #ebebeb;
    box-shadow: 0 0 1px 1px #ebebeb;
    background-color: #fff;
    z-index: 100;
    overflow: hidden;
  }

  .music-playing > div {
    float: left;
    height: 2.5rem;
  }

  .thum > img {
    width: 100%;
    height: 100%;
  }

  .thum {
    padding: .2rem;
    width: 2.5rem;
    height: 2.5rem;
    box-sizing: border-box;
  }

  .music-info {
    width: calc(100% - 7rem);
    box-sizing: border-box;
    padding-top: .5rem;
    font-size: 14px;
    line-height: .9rem;
  }

  .music-info > p:last-of-type {
    font-size: 12px;
    color: #6f6f6f;
  }

  .play-icon {
    text-align: center;
    width: 4.5rem;
    font-size: 1.3rem;
    line-height: 2.5rem;
  }
  .icon-play::before{
    content: '\e624';
    font-size: 1.2rem;
    font-family: inherit;
    animation: Scale1 .2s ease;
  }
  .icon-stop::before{
    content: '\e634';
    font-size: 1.2rem;
    font-family: inherit;
    animation: Scale .2s ease;
  }
</style>



